  }
}
/* Navigation base, used for both desktop & mobile navigation */
.nav-sidebar .dashboard-link:not(.active) a:hover {
  background: rgba(57, 63, 68, 0.5) !important;
}
.nav-item-pf-header {
  color: #fff;
  font-size: 16px;
  margin: 18px 20px 10px 20px;
}
.area-ct-subnav {
  background: #393f44;
  border-right: 1px solid #292e34;
  -webkit-flex: none;
  flex: none;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
  flex-direction: column;
  position: relative;
  z-index: 2;
  opacity: 1;
  /* Fade start and end of the host menu to indicate more content is available */
}
.area-ct-subnav .list-group {
  border: none;
  margin: 10px 0;
}
.area-ct-subnav .list-group-item {
  padding: 0;
  background: inherit;
  border: none;
  z-index: 1;
}
.area-ct-subnav .list-group-item,
.area-ct-subnav .list-group-item > a {
  transition: all 300ms;
}
.area-ct-subnav .list-group-item:hover,
.area-ct-subnav .list-group-item:hover > a {
  transition: all 150ms;
}
.area-ct-subnav .list-group-item:hover,
.area-ct-subnav .list-group-item.active {
  /* Move hover & selected above overflow fade */
  z-index: 3;
}
.area-ct-subnav .list-group-item:hover a,
.area-ct-subnav .list-group-item.active a {
  color: #fff;
}
.area-ct-subnav .list-group-item:hover {
  background: rgba(77, 82, 88, 0.5);
}
.area-ct-subnav .list-group-item.active {
  background: #4d5258;
}
.area-ct-subnav .list-group-item.active a {
  font-weight: 600;
}
.area-ct-subnav .list-group-item a {
  padding: 10px 15px;
  display: block;
  color: #d1d1d1;
  text-decoration: none;
  transition: all 300ms;
}
.area-ct-subnav .dropdown-toggle {
  position: relative;
  overflow: visible !important;
}
.area-ct-subnav .dropdown-toggle:after,
.area-ct-subnav:after {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  height: 2rem;
  pointer-events: none;
  z-index: 2;
  /* Accomodate scrollbars with margin (IE11 needs 18px; rest are less) */
