  background: linear-gradient(to bottom, #393f44, rgba(57, 63, 68, 0));
  margin-top: 1px;
  bottom: -2rem;
}
.area-ct-subnav:after {
  background: linear-gradient(to top, #393f44, rgba(57, 63, 68, 0));
  bottom: 0;
  height: 4rem;
}
.nav-sidebar > .list-group .dashboard-link {
  display: block;
}
.nav-sidebar > .list-group .dashboard-link:not(.active) .fa {
  color: #72767b !important;
}
.machine-dropdown {
