/* Desktop navigation */
@media screen and (min-width: 768px) {
  .area-ct-navbar {
    -webkit-flex: none;
    flex: none;
    min-width: 100%;
  }
  .area-ct-content {
    -webkit-flex: auto;
    flex: auto;
    position: relative;
    /* Position as absolute to make things work on old Chrome versions,
           mainly to make tests pass */
  }
  .area-ct-content > iframe {
    position: absolute;
  }
  .area-ct-content > .curtains-ct {
    position: absolute !important;
  }
  .area-ct-content > iframe,
  .area-ct-content > .curtains-ct {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  .area-ct-body {
    position: relative;
    -webkit-flex: auto;
    flex: auto;
    z-index: 3;
    display: -webkit-flex;
    display: flex;
    /* IE11 needs to have the flex-basis set to page max,
           else it stretches too tall */
    -webkit-flex-basis: 100%;
    flex-basis: 100%;
  }
  .nav-pf-vertical {
    position: static !important;
    height: 100%;
    border-right-width: 0;
    top: 0;
  }
  .nav-sidebar-wrap {
    -webkit-flex: none;
    flex: none;
    z-index: 3;
  }
  .nav-sidebar-wrap:not(.expanded) {
    width: 68px;
  }
  .nav-sidebar-wrap:not(.expanded) .nav-sidebar {
    width: 68px;
  }
  .nav-sidebar-wrap:not(.expanded) .list-group-item-value {
    opacity: 0;
    transition: opacity 150ms ease-out;
  }
  .nav-sidebar {
    /* Use absolute position to make old Chrome (2015 Dec) tests pass */
    position: absolute !important;
    height: 100%;
    transition: all 150ms ease-out;
    box-shadow: 0 0;
    background: linear-gradient(to left, #030303, #292e34 1px);
  }
  .nav-sidebar span.first-letter {
    font-size: 0px !important;
    color: #d1d1d1;
  }
  .nav-sidebar span.first-letter:first-letter {
    font-size: 22px !important;
  }
  .nav-sidebar .fa {
    margin-left: -4px;
    margin-top: -1px;
  }
  .nav-sidebar .fa:not(.first-letter) {
    font-size: 22px !important;
  }
  .nav-sidebar .list-group-item {
    border-right-width: 0;
    overflow: hidden;
  }
  .nav-sidebar .list-group-item > a {
    padding-right: 20px;
  }
  .nav-sidebar .list-group-item:focus {
    background: active;
  }
  .nav-sidebar-wrap:not(.expanded) .nav-sidebar:not(.clicked):hover {
    width: 180px;
    box-shadow: 0 0 3px rgba(0, 0, 0, 0.5), 0 0 32px rgba(0, 0, 0, 0.75);
    transition-duration: 500ms;
    transition-timing-function: cubic-bezier(0, 1.2, 0.8, 1);
  }
  .nav-sidebar-wrap:not(.expanded) .nav-sidebar:not(.clicked):hover .list-group-item-value {
    transition-duration: 300ms;
    opacity: 1;
  }
  .nav-sidebar-wrap:not(.expanded) .nav-sidebar:not(.clicked):hover .first-letter {
    opacity: 0;
  }
  .single-nav .nav-sidebar {
    display: none;
  }
  .single-nav .nav-sidebar-wrap {
    width: 0px;
  }
  .single-nav .area-ct-subnav {
    left: 0px;
  }
  .area-ct-layout {
    display: -webkit-flex;
    display: flex;
    width: 100%;
    height: 100%;
    -webkit-flex-flow: column;
    flex-flow: column;
    align-content: stretch;
  }
  .area-ct-subnav {
    visibility: visible !important;
    top: 0;
    left: 0;
    bottom: 0;
    width: 15em;
  }
}
/* Mobile navigation */
@media screen and (max-width: 767px) {
  /* Use native mobile fonts (when available) for nav & menu text */
  .area-ct-navbar,
  .area-ct-body {
    font-family: roboto, san francisco, helvetica light, noto, helvetica, sans-serif;
    font-size: 14px;
    align-content: stretch;
    align-items: stretch;
  }
  .area-ct-navbar a,
  .area-ct-body a {
    font-size: 14px !important;
    padding: 12px 15px;
  }
  /* Layout UI in CSS grids
    (all mobile browsers support this new layout type)

    How this works:

    CSS grids version 1 is now implemented in all mobile browsers; we can use it.
    However, display: container and subgrids are not implemented in Chrome yet.
    To work around this, we do a little more complex gridding by overlapping
    child grids on top of the parent grid. We basically set up the main grid
    as with a gutter of @mobile-button-size ("56px") on the top, right side,
    and bottom. We then stretch the child grids to this.

    The elements are then assigned grid names with 'grid-area' and placed in the
    grid using 'grid-template-areas', based on their immediate parent (which is
    set as a grid display type).

    Some grid-areas override their template placement with grid-columns and
    grid-rows. The most notible being the navmenu, so that it can overlap
    content. The value for grid-colums is either a single number or first/last+1
    (which basically means go from first and stop when it hits the grid
    column/row _after_ the one you want to last fill).

    Note: Due to less, we have to wrap some of the values in ~"" so that they
    will be passed directly to the resulting CSS... else less will want to try
    to do math, which isn't want we want.

    Tip: Firefox dev tools let you visualize grids in the "layout" subtab of the
    "inspector" tab.
    */
  .area-ct-layout,
  .area-ct-body,
  .area-ct-navbar {
    display: grid;
    grid-template-columns: auto 56px;
    grid-template-rows: 56px 1fr 56px;
    align-content: end;
  }
  .area-ct-layout {
    grid-template-areas: "content content" "content content" "nav topbar";
    height: 100%;
    width: 100%;
  }
  .area-ct-body {
    /* Stretch the nav wrapper to the same area on the parent grid */
    grid-column: 1/3;
    grid-row: 1/4;
    grid-template-rows: 56px calc(100% - 56px * 2) 56px;
    z-index: 2;
    /* Pass through mouse events */
    pointer-events: none;
    grid-template-areas: "content content" "content content" "nav empty";
  }
  .area-ct-body > * {
    /* Allow mouse events for children */
    pointer-events: auto;
  }
  .area-ct-navbar {
    /* Create another child grid and stretch it to the parent grid */
    pointer-events: none;
    display: grid;
    background: transparent;
    border: none;
    grid-template: "notmenu menu menu" "notheader notheader header";
    grid-area: topbar;
    grid-column: 1/3;
    /* Expand to the whole column */
    grid-row: 2/4;
    grid-template-rows: auto 56px;
    grid-template-columns: minmax(56px, auto) auto 56px;
    z-index: 3;
  }
  .area-ct-navbar > * {
    pointer-events: auto;
  }
  .area-ct-navbar:before,
  .area-ct-navbar:after {
    display: none;
  }
  .navbar-header {
    grid-area: header;
    background: #292e34;
  }
  .navbar-collapse {
    grid-area: menu;
    align-self: end;
    background: #292e34;
  }
  .navbar-brand {
    display: none;
  }
  .navbar-toggle {
    float: right !important;
    margin: 0 !important;
    width: 100%;
    height: 100%;
  }
  .area-ct-content {
    display: block;
    grid-area: content;
    z-index: 1;
  }
  .area-ct-content > iframe {
    animation: navFadeIn 300ms;
  }
  .nav-sidebar-wrap {
    grid-area: nav;
    z-index: 3;
    max-width: calc(100vw - 56px);
    overflow: auto;
    overflow-y: hidden;
    position: relative;
  }
  .nav-sidebar {
    position: static !important;
    width: auto;
    max-width: 100%;
    overflow: hidden;
    overflow-x: auto;
    background: linear-gradient(to bottom, #030303, #292e34 1px);
  }
  .nav-sidebar ul,
  .nav-sidebar li {
    padding: 0;
    margin: 0;
  }
  .nav-sidebar > .list-group {
    position: relative;
    display: -webkit-flex;
    display: flex;
  }
  .nav-sidebar > .list-group li {
    -webkit-flex: auto;
    flex: auto;
  }
  .nav-sidebar .list-group-item {
    width: auto !important;
    height: 100% !important;
    margin: 0 !important;
    padding: 0 !important;
    position: static;
    max-width: 33vw;
  }
  .nav-sidebar .list-group-item a {
    display: inline-block !important;
    padding: 6px 12px !important;
    padding: 0;
    text-align: center;
    width: auto !important;
    min-width: 80px;
    width: 100% !important;
    height: 56px !important;
    margin-bottom: 12px;
  }
  .nav-sidebar .list-group-item.active {
    z-index: 2;
  }
  .nav-sidebar .list-group-item.active a {
    background: #393f44 !important;
  }
  .nav-sidebar .list-group-item.active a:before {
    bottom: 0 !important;
    left: 0 !important;
    right: 0 !important;
    top: auto !important;
    height: 3px !important;
    width: auto !important;
    z-index: 3;
  }
  .nav-sidebar .list-group-item.active a:after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    border: 1px solid black;
    border-width: 0 1px;
    pointer-events: none;
    opacity: 0.5;
    z-index: 4;
  }
  .nav-sidebar .list-group-item.active:first-child a:after {
    border-left: none;
  }
  .nav-sidebar .fa,
  .nav-sidebar .pficon-container-node,
  .nav-sidebar .list-group-item-value {
    display: inline-block;
    line-height: 1 !important;
    font-weight: 400;
  }
  .nav-sidebar .fa,
  .nav-sidebar .pficon-container-node {
    margin: 0 auto !important;
    float: none !important;
    font-size: 24px !important;
  }
  .nav-sidebar .list-group-item-value {
    font-size: 12px !important;
    line-height: 2 !important;
    margin: -6px auto 0;
    padding: 0 !important;
  }
  /* Restrict width of hostname nav label in mobile */
  #host-nav-item .list-group-item-value {
    max-width: calc(20vw - 24px);
  }
  .area-ct-subnav {
    display: -webkit-flex !important;
    display: flex !important;
    align-self: end;
    grid-area: navmenu;
    max-height: 100%;
    min-height: 50%;
    grid-column: 1;
    grid-row: 2;
    z-index: 2;
    position: relative;
    overflow: hidden;
    opacity: 0;
    transform: translate(0, 100%) scaleY(0);
    transition: opacity 200ms ease-in 100ms, transform 100ms ease-in 300ms;
  }
  .area-ct-subnav.interact {
    transform: none;
    opacity: 1;
    overflow: visible;
